import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import {
  ReanderField,
  ReanderSelect,
} from "../../../../components/notification/notification";
import CetakMekanik from "./CetakMekanik";

class HeadLaporanMekanik extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="row">
        <div className="col-lg-3">
          <Field
            name="tanggal_awal"
            component={ReanderField}
            type="date"
            label="Dari Tanggal"
            placeholder="Masukan Tanggal Awal"
          />
        </div>
        <div className="col-lg-3">
          <Field
            name="tanggal_akhir"
            component={ReanderField}
            type="date"
            label="Sampai Tanggal"
            placeholder="Masukan Sampai Tanggal"
          />
        </div>
        <div className="col-lg-3">
          <Field
            name="kode_mekanik"
            component={ReanderSelect}
            options={[
              { value: "MEKANIK01", name: "MEKANIK 01" },
              { value: "MEKANIK02", name: "MEKANIK 02" },
              { value: "MEKANIK03", name: "MEKANIK 03" },
              { value: "MEKANIK04", name: "MEKANIK 04" },
            ]}
            type="text"
            label="Kode Mekanik"
            placeholder="Masukan Kode Mekanik"
          />
        </div>
        <div className="col-lg-12">
          <div className="text-right">
            <button
              className="btn btn-primary"
              onClick={() =>
                CetakMekanik(
                  "2 Februari 2021",
                  "SEMUA",
                  "ADMIN",
                  "2 FEBRUARI 2021",
                  "ADMIN"
                )
              }
            >
              Lihat Data <i className="fa fa-print ml-3"></i>
            </button>
          </div>
        </div>
      </div>
    );
  }
}

HeadLaporanMekanik = reduxForm({
  form: "HeadLaporanMekanik",
  enableReinitialize: true,
})(HeadLaporanMekanik);
export default connect()(HeadLaporanMekanik);
